<template>
  <div className="container"> 
    <div v-if="!Object.keys(mycart).length"> 
      <h2> There are no items in your Cart </h2>
      </div>

  <div v-if="Object.keys(mycart).length"> 
    <!-- In the object, if we can find just one key, then let us display it. 
      That is simply what this condition states -->
  <div className="cflex" v-for="(oneitem, key, i) in mycart" :key="i">
    <div className="pimgc"><h4> 
      <div className="bg3"> 
        <img src="../assets/kilishipack.png"> </div> 
        </h4>
      </div>
        
        <div class="dc"><h4> {{key}} </h4>
        <div className="dcdescription"><h3> Quantity: {{oneitem}} </h3></div></div>
        <div className="pc"><h4>{{getprice(key)}}</h4></div>
        <div class="dec" @click="remove(key)"><h4> X </h4></div>
        </div>
      </div>

  </div>
</template>
<script> 
export default {
  props: ['mycart', 'getprice', "remove"]
}
</script>

<style src="../assets/Cart.css">
</style> 